  text-align: left
}
.navigation ul {
  line-height: 0;
  font-size: 0;
  letter-spacing: -1px
}
.navigation li {
  position: relative;
  display: inline-block;
  vertical-align: middle;
  line-height: 1;
  font-size: 1.3rem;
  text-transform: uppercase;
  letter-spacing: 0;
  white-space: normal
}
.navigation li.active > a:not(.custom-btn):after,
.navigation li:hover > a:not(.custom-btn):after {
  left: 0;
  width: 100%;
  opacity: 1;
  visibility: visible
}
.navigation li a:not(.custom-btn) {
  position: relative;
  display: inline-block;
  line-height: inherit;
  font-size: inherit;
  font-family: inherit;
  transition: background-color .3s ease-in-out,border-color .3s ease-in-out,color .3s ease-in-out
}
.navigation li a:not(.custom-btn):after,
.navigation li a:not(.custom-btn):before {
  pointer-events: none
}
.navigation li a:not(.custom-btn):after {
  content: "";
  display: block;
  position: absolute;
  top: 100%;
  left: 50%;
  width: 0;
  height: 2px;
  background-color: #fcdb5a;
  opacity: 0;
  visibility: hidden;
  transition: left .3s ease-in-out,width .3s ease-in-out,opacity .3s ease-in-out,visibility .3s ease-in-out
}
@media screen and (prefers-reduced-motion:reduce) {
  .navigation li a:not(.custom-btn),
  .navigation li a:not(.custom-btn):after,
  .start-screen__content__item,
  .top-bar__navigation-toggler span,
  .top-bar__navigation-toggler span:after,
  .top-bar__navigation-toggler span:before,
  a {
    transition: none
  }
}
.custom-btn {
  position: relative;
  display: inline-block;
  vertical-align: middle;
  padding-left: 28px;
  padding-right: 28px;
  line-height: 1;
  font-size: 1.2rem;
  font-family: Raleway,sans-serif;
  font-weight: 700;
  text-align: center!important;
  text-decoration: none!important;
  text-shadow: none!important;
  text-transform: uppercase;
  letter-spacing: 0;
  color: #4f4a37;
  border: 2px solid #fcdb5a;
  border-radius: 30px;
  box-shadow: none;
  outline: 0;
  cursor: pointer;
  user-select: none;
  -webkit-user-drag: none;
  user-drag: none;
  -ms-touch-action: manipulation;
  touch-action: manipulation;
  transition: background-color .25s ease-in-out,border-color .25s ease-in-out,color .25s ease-in-out
}
.custom-btn:focus,
.custom-btn:hover {
  color: #4f4a37
}
.custom-btn--tiny {
  min-width: 85px;
  min-height: 35px;
  padding-top: 11px;
  padding-bottom: 10px;
  font-size: 1rem
}
.custom-btn--small {
  min-width: 160px;
  min-height: 45px;
  padding-top: 15px;
  padding-bottom: 14px
}
.custom-btn--medium {
  min-width: 160px;
  min-height: 54px;
  padding-top: 19px;
  padding-bottom: 19px
}
.custom-btn--big {
  min-width: 200px;
  min-height: 65px;
  padding-top: 24px;
  padding-bottom: 24px;
  font-size: 1.3rem
}
.custom-btn.custom-btn--style-1 {
  background-color: transparent
}
.custom-btn.custom-btn--style-1:focus,
.custom-btn.custom-btn--style-1:hover,
.custom-btn.custom-btn--style-2 {
  background-color: #fcdb5a
}
.custom-btn.custom-btn--style-2:focus,
.custom-btn.custom-btn--style-2:hover {
  background-color: transparent
}
.custom-btn.custom-btn--style-3 {
  background-color: transparent;
  color: #fff
}
.custom-btn.custom-btn--style-3:focus,
.custom-btn.custom-btn--style-3:hover {
  background-color: #fcdb5a;
  color: #4f4a37
}
.custom-btn.custom-btn--style-4 {
  background-color: #fcdb5a
}
.custom-btn.custom-btn--style-4:focus,
.custom-btn.custom-btn--style-4:hover {
  background-color: transparent;
  color: #fff
}
.custom-btn.custom-btn--style-5 {
  background-color: #fff;
  border-color: #fff;
  color: #1b1b1b
}
.custom-btn.custom-btn--style-5:focus,
.custom-btn.custom-btn--style-5:hover {
  background-color: #fcdb5a;
  border-color: #fcdb5a;
  color: #4f4a37
}
.custom-btn.wide {
  width: 100%
}
form {
  position: relative
}
form .input-wrp {
  position: relative;
  display: block;
  width: 100%;
  line-height: 1;
  margin-top: 20px
}
form .textfield {
  display: block;
  float: none;
  width: 100%;
  background: padding-box none;
  border: none;
  border-bottom: 1px solid #dadada;
  padding: 13px 0;
  line-height: 1.2;
  font-size: 1.4rem;
  font-family: "Open Sans",sans-serif;
  font-weight: 500;
  color: #666;
  appearance: none;
  outline: 0;
  box-shadow: none;
  border-radius: 0;
  transition: background-color .3s ease-in-out,border-color .3s ease-in-out,color .3s ease-in-out
}
form .textfield::-webkit-input-placeholder {
  color: #ccc;
  transition: color .3s ease-in-out
}
@media screen and (prefers-reduced-motion:reduce) {
  .custom-btn,
  form .textfield {
    transition: none
  }
  form .textfield::-webkit-input-placeholder {
    transition: none
  }
}
form .textfield::-moz-placeholder {
  color: #ccc;
  transition: color .3s ease-in-out
}
@media screen and (prefers-reduced-motion:reduce) {
  form .textfield::-moz-placeholder {
    transition: none
  }
}
form .textfield:-moz-placeholder {
  color: #ccc;
  transition: color .3s ease-in-out
}
@media screen and (prefers-reduced-motion:reduce) {
  form .textfield:-moz-placeholder {
    transition: none
  }
}
form .textfield:-ms-input-placeholder {
  color: #ccc;
  transition: color .3s ease-in-out
}
form .textfield.error {
  border-color: #fcdb5a!important
}
form .nice-select.textfield,
form input.textfield,
form select.textfield {
  height: 44px
}
form select.js-select {
  width: 0;
  height: 0
}
form textarea {
  resize: vertical;
  min-height: 125px;
  height: 100%
}
form button[type=submit] {
  cursor: pointer;
  box-shadow: none;
  outline: 0;
  margin-top: 55px
}
.form--horizontal {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: flex-end
}
.form--horizontal .input-wrp,
.form--horizontal button[type=submit] {
  margin-top: 0
}
.form--horizontal .input-wrp {
  flex: 1;
  padding-right: 20px
}
label {
  cursor: pointer
}
.checkfield {
  position: relative;
  display: inline-block;
  margin-top: 20px;
  line-height: 1.2;
  padding-left: 30px
}
.checkfield input[type=checkbox] {
  position: absolute;
  top: 0;
  left: 0;
  width: 0;
  height: 0;
  visibility: hidden;
  opacity: 0;
  clip: rect(2px,2px,2px,2px)
}
.checkfield input[type=checkbox]:checked ~ i {
  background-color: #fcdb5a;
  border-color: #fcdb5a
}
.checkfield input[type=checkbox]:checked ~ i:before {
  transform: scale(1) rotate(45deg)
}
.checkfield input[type=checkbox]:checked ~ span a {
  color: #fcdb5a
}
.checkfield i {
  position: relative;
  float: left;
  width: 14px;
  height: 14px;
  margin-top: 1px;
  margin-left: -30px;
  padding-left: 1px;
  background-color: #fff;
  border: 1px solid #d6d6d6;
  font-size: 1.6rem;
  font-weight: 700;
  text-align: center;
  overflow: hidden;
  border-radius: 3px;
  transition: background-color .25s ease-in-out,border-color .25s ease-in-out
}
@media screen and (prefers-reduced-motion:reduce) {
  form .textfield:-ms-input-placeholder {
    transition: none
  }
  .checkfield i {
    transition: none
  }
}
.checkfield i:before {
  content: "";
  display: block;
  width: 5px;
  height: 7px;
  margin: 2px auto auto;
  border: 2px solid #333;
  border-top: none;
  border-left: none;
  transform: scale(0) rotate(45deg);
  transition: transform .25s cubic-bezier(.23,1,.32,1)
}
@media screen and (prefers-reduced-motion:reduce) {
  .checkfield i:before {
    transition: none
  }
}
.checkfield i:last-child {
  margin-right: 0
}
.textfield.nice-select {
  color: #ccc;
  padding-right: 30px
}
.textfield.nice-select:after {
  width: 7px;
  height: 7px;
  right: 5px;
  margin-top: -5px
}
.textfield.nice-select.open,
.textfield.nice-select:active,
.textfield.nice-select:focus {
  border-color: #dadada
}
.textfield.nice-select .list {
  padding-top: 5px;
  padding-bottom: 5px;
  color: #888;
  box-shadow: 0 0 0 1px rgba(0,0,0,.05)
}
.textfield.nice-select .option {
  min-height: 35px;
  padding-left: 20px;
  line-height: 35px
}
.textfield.nice-select .option.focus,
.textfield.nice-select .option.selected.focus,
.textfield.nice-select .option:hover {
  background-color: #f7f7f7!important
}
.textfield.nice-select .option.selected {
  font-weight: 600
}